/*--------------------
Body
--------------------*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

$color: #e24040;

body {
  background-color: transparent !important;
  color: #5a6674 !important;
  font-size: 13px;
  font-family: 'Roboto', sans-serif;
}

/*--------------------
App
--------------------*/
.search-form {
  position: relative;
  left: 50%;
  width: 90%;
  height: 40px;
  border-radius: 40px;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
  transform: translate(-50%, 0%);
  background: #fff;
  transition: all 0.3s ease;
  overflow:visible;

  &.focus {
    box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  }
}

.search-input {
  position: absolute;
  top: 10px;
  left: 38px;
  font-size: 14px;
  background: none;
  color: #5a6674;
  height: 20px;
  border: none;
  appearance: none;
  outline: none;

  &::-webkit-search-cancel-button {
    appearance: none;
  }
}

.search-button {
  position: absolute;
  top: 10px;
  left: 15px;
  height: 20px;
  width: 20px;
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  outline: none!important;
  cursor: pointer;
  
  & svg {
    width: 20px;
    height: 20px;
    fill: #5a6674;
  }
}

.search-option {
  position: absolute;
  text-align: right;
  top: -4px;
  right: 35px;
  border: none;
  outline: none;

  div {
    position: relative;
    display: inline-block;
    margin: 0 1px;
    cursor: pointer;
    
    button {
      position: absolute;
      top: 0;
      left: 0;
      background-color: transparent;
      border: none;
      outline: none;
      cursor: pointer;
    }

    svg {
      height: 20px;
      width: 20px;
      fill: #5a6674;
      opacity: 0.6;
      transition: all .2s ease-in-out;
      pointer-events: none;
    }

    .recording {
      fill: $color;
      opacity: 1;
    }
    
    &:hover svg {
      opacity: 1;
    }
  }
}

.dropdown {
  display: flex;
  overflow: hidden;
  top: 130%;
  flex-direction: column;
  //border: 1px solid gray;
  border-radius: 10px;
}

.scroll {
  overflow-x: hidden;
}

.scroll::-webkit-scrollbar {
  width: 10px;
}

.scroll::-webkit-scrollbar-thumb {
  background-color: lightgray;
  border-radius: 20px;
  border: 3px solid lightgray;
}

.scroll::-webkit-scrollbar-thumb:hover {
  background-color: rgb(194, 194, 194);
  border: 3px solid rgb(194, 194, 194);
}

.dropdown:empty {
  border: none;
}

.dropdown-row {
  padding: 10px;
  cursor: pointer;
  text-align: start;
  margin: 0;
}

.dropdown-row:hover {
  background-color: #eee;
}
